const safeGet = (target, pathArray, defaultValue) => {
  if (!target || !pathArray || !(pathArray instanceof Array)) {
    return defaultValue
  }
  if (pathArray.length == 0) {
    return target
  }
  const [head, ...tail] = pathArray
  return safeGet(target[head], tail, defaultValue)
}

export default {safeGet}