import React from 'react'
import {Breadcrumb} from 'antd'
import Layout from './component/layout'
import MainPage from './pages/main'
import ProjectListPage from './pages/project-list'
import DailyListPage from './pages/daily-list'

const {Item} = Breadcrumb

const BreadTitle = props => {
  const {activeKey} = props;
  return (
    <Breadcrumb style={{ margin: '16px 0' }}>
      <Item>{activeKey === 'main' ? 'Home' : (activeKey === 'project-list' ? 'Project' : 'Daily')}</Item>
      <Item>{activeKey === 'main' ? '' : 'List'}</Item>
    </Breadcrumb>
  )
}

class App extends React.Component {
  state = {
    activeKey: 'main'
  }

  onMenuChange = activeKey => {
    console.log(activeKey)
    this.setState({activeKey})
  }

  render() {
    const {activeKey} = this.state
    return (
      <Layout onMenuChange={this.onMenuChange}>
        <BreadTitle activeKey={activeKey}/>
        <div style={{ padding: 24, background: '#fff', minHeight: 360 }}>
          {
            activeKey === 'main' ? <MainPage /> : (
              activeKey === 'project-list' ? <ProjectListPage /> : <DailyListPage />
            )
          }
        </div>
      </Layout>
    )
  }
};

export default App;
