import React from 'react'
import { Table, Input, Button, Modal } from 'antd'
import './index.css'
import utils from '../../utils'
const { webApi, format } = utils
const { Search } = Input

const columns = [{
  title: '内部名称',
  dataIndex: 'projectName',
}, {
  title: '外部名称',
  dataIndex: 'projectOuterName',
}, {
  title: '项目状态',
  dataIndex: 'projectStatus',
}, {
  title: '风险级别',
  dataIndex: 'riskLevel',
}, {
  title: '所属',
  dataIndex: 'projectType',
}, {
  title: '硬件平台',
  dataIndex: 'hardwarePlatform',
}, {
  title: '硬件型号',
  dataIndex: 'platformModel',
}, {
  title: '总负责人',
  dataIndex: 'leaderId',
}, {
  title: '项目负责人',
  dataIndex: 'projectLeaderId',
}];

const rowSelection = {
  onChange: (selectedRowKeys, selectedRows) => {
    console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
  },
  getCheckboxProps: record => ({
    disabled: false,
    name: record.name,
  }),
};

class App extends React.Component {
  state = {
    projectList: [1],
    modalVisible: false,
    confirmLoading: false,
    current: 1,
    pageSize: 10,
    total: 100
  }

  handleClickCreate = () => {
    this.setState({modalVisible: true})
  }

  componentWillMount() {
    webApi.queryProject({}).then(res => {
      const projectList = format.safeGet(res, ['data', 'projects'], [])
      this.setState({projectList})
    })
  }

  handleSearch = searchText => {
    searchText = searchText ? searchText.trim() : ''
    if (!searchText) {
      return
    }
    console.log(searchText)
  }

  handleModalOk = () => {
    this.setState({confirmLoading: true})
    setTimeout(() => {
      this.setState({
        confirmLoading: false,
        modalVisible: false
      })
    }, 2000)
  }

  handleModalCancle = () => {
    this.setState({modalVisible: false})
  }

  onPageChange = (pageNo, pageSize) => {
    this.setState({current: pageNo, pageSize})
  }

  render() {
    const {modalVisible, confirmLoading, projectList = [], current, pageSize, total} = this.state
    return (
      <div className='project-list-container'>
        <div className='search-header'>
          <Search placeholder="text" onSearch={this.handleSearch} style={{ width: 200 }}/>
          <Button type="primary" onClick={this.handleClickCreate}>Create</Button>
        </div>
        <Table columns={columns} dataSource={projectList} pagination={{current, pageSize, total, onChange: this.onPageChange}} />
        <Modal title="Add a new project" visible={modalVisible} onOk={this.handleModalOk} confirmLoading={confirmLoading} onCancel={this.handleModalCancle}>
          <p>hello world</p>
        </Modal>
      </div>
    )
  }
};

export default App;
